@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?mwdwch');
  src:  url('../fonts/icomoon.eot?mwdwch#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?mwdwch') format('truetype'),
    url('../fonts/icomoon.woff?mwdwch') format('woff'),
    url('../fonts/icomoon.svg?mwdwch#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-add:before {
  content: "\e903";
}
.icon-delete:before {
  content: "\e902";
}
.icon-no-select:before {
  content: "\e900";
}
.icon-select:before {
  content: "\e901";
}



